import React, { Component } from 'react';
import OrdererName from "./OrdererName/OrdererName.js";
import CoffeeSelect from "./CoffeeSelect/CoffeeSelect.js";
import MakeTable from "./OrderList/OrderList.js";
import { Button, Form, FormGroup } from 'reactstrap';
import './Main.css';

var Members = [
    {value:''},
    {value:''},
    {value:''}
];

class Main extends Component {
    curName="";
    curMenu="";
    orders = [];

    state = {
        index:1,
    }

    _onChangeName = (e) => {
        this.curName = e.target.value;
    }

    _onChangeMenu = (e) => {
        this.curMenu = e.target.value;
    }

    _onClick = () => {
        if (this.curName === ""){
            console.log("name is Required");
            return;
        }
        if (this.curMenu === ""){
            console.log("Menu is Required");
            return;
        }
        const newOrder = {
            key:this.state.index,
            orderer_name:this.curName,
            order_menu:this.curMenu
        };
        if(!this._updateListIfFound(newOrder)){
            this._addList(newOrder);
        }
        else{
            this.forceUpdate();
        }
        // this.setState({
        //         orders:[
        //             ...this.state.orders,
        //             {
        //                 key:this.index,
        //                 orderer_name:this.curName,
        //                 order_menu:this.curMenu
        //             }
        //         ]
        // });
    }

    _addList = (order) => {
        this.orders.push({
            key:order.key,
            orderer_name:order.orderer_name,
            order_menu:order.order_menu
        });
        this.setState({
            index : this.state.index + 1
        });
    }

    _updateListIfFound = (order) => {
        var found_index = this.orders.findIndex((list_order) => {
            return list_order.orderer_name === order.orderer_name;
        });
        if (found_index === -1){
            return false;
        }
        order.key = this.orders[found_index].key;
        this.orders.splice(found_index, 1, order);
        return true;
    }

    // xs, sm, md, lg :        
    // 12   
    render() {

        return (
            <Form>
                <FormGroup align="center">
                    <h2> </h2>
                </FormGroup>
                <OrdererName names={Members} callback={this._onChangeName}/>
                <CoffeeSelect changedMenu={this._onChangeMenu}/>
                <FormGroup align="center">
                    <Button color="primary" onClick={this._onClick}></Button>
                </FormGroup>
                <FormGroup align="center">
                    <MakeTable List={this.orders}/>
                </FormGroup>
            </Form>
        );
    }
}

export default Main;



