import React from 'react';
import { Col, FormGroup, Label, Input } from 'reactstrap';
import './OrdererName.css';

function OrdererName (props) {
    // xs, sm, md, lg :        
    return (
        <FormGroup row>
        <Col xs={1}/>
        <Col xs={3}>
        <Label for="nameSelect"> :</Label>
        </Col>
        <Col xs={7}>
        <Input type="select" name="select" id="nameSelect" onChange={props.callback}>
        <option value=""> </option>
        {
            props.names.map((e, index) => {
                return (
                    <option key={index}>{e.value}</option>
                )
            })
        }
        </Input>
        </Col>
        </FormGroup>
    );
}

export default OrdererName;



