import React from 'react';
import { Table } from 'reactstrap';
import './OrderList.css';

function MakeOrder({orders}){
    //console.log(orders);
    const order = orders ? orders.map((e) => {
        return(
            <tr key={e.key}>
                <th scope="row">{e.key}</th>
                <td>{e.orderer_name}</td>
                <td>{e.order_menu}</td>
            </tr>
        );
    }) : "No orders";
    return order;
}

function MakeTable({List}) {
    return(<Table>
        <thead>
            <tr>
                <th>#</th>
                <th></th>
                <th></th>
            </tr>
        </thead>
        <tbody>
            <MakeOrder orders={List}/>
        </tbody>
        </Table>);
    }



    export default MakeTable;



